import Bar from '../components/Bar.vue';
import authApi from '../../service/auth';
import global from '../../utils/global.js';
import babyApi from "../../service/baby";

export default {
	components:{
		Bar
	},
	data(){
		return{
			// 自定义导航栏对象
			setNav:{
				'bg':'#8686F7',  //背景色
				'color':'#fff',  //字体颜色
				'isdisPlayNavTitle':true, //是否显示返回按钮，由于导航栏是共用的，把所有的东西封装好，
				// 然后有些页面不需要的东西通过条件控制进行显示与隐藏
				'navTitle':'' //导航标题
			},
			isShow: false,
			userInfo: {},
			imageURL: '../../static/img/5.jpg',
			babyList: 0
		}
	},
	async onLoad(options){
	  const login = await authApi.login();
	  if(login){
		this.userInfo = JSON.parse(global.getUser());
		console.log(this.userInfo);
		this.getbabyList();
		this.isShow = true;
	  }else{
		this.isShow = false
	  }
	  
	},
	methods: {
		//列表
		getbabyList(){
		 	babyApi.getBabyList().then(res=>{
		 		if(res.code == "0000"){
		 			this.babyList = res.data.length;
		 		}else{
		 			uni.showToast({
		 				icon:"none",
		 				title: res.responseMsg
		 			});
		 		}
		 	});
		},
		toMybaby(){
			if(this.checkLogin("/pages/baby/babyInfo")){
				uni.navigateTo({
					url: "/pages/my/myBaby"
				});
			}
		},
		toPolicy(){
			if(this.checkLogin("/pages/my/insurancePolicy")){
				uni.navigateTo({
					url: "/pages/my/insurancePolicy"
				});
			}
		},
		toSetup(){
			uni.navigateTo({
				url: "/pages/my/setup"
			});
		},
		toAdvice(){
			uni.navigateTo({
				url: "/pages/my/advice"
			});
		},
		toLogin(){
			uni.navigateTo({
				url:"/pages/login/login"
			});
		},
		toceshi(){
			uni.navigateTo({
				url:"/pages/vaccine/vaccKnow"
			});
		},
	}
}
